@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;500;700;900&family=Poppins:wght@300;400;500;600;700&display=swap');
/* font-family: 'Montserrat', sans-serif; */

:root {
    /* ALL project colors */

    ----primaryColor: hsl(228, 66%, 53%);
    ---primaryColorLight: hsl(223, 100%, 97%);
    --primaryColorDark: hsl(216, 58%, 28%);
    ---gradientLight: rgb(114, 145, 255);
    --bgColor: hsl(0, 0%, 100%);
    ---formColor: hsl(20, 42%, 87%);
    ---whiteColor: hsl(240, 25%, 97%);
    ---blackColor: hsl(0, 0%, 18%);
    --blackColorLight: hsl(233, 3%, 51%);
    ---shadowColor: hsl(219, 63%, 95%);
    --inputColor: hsl(330, 12%, 97%);
    ---cardColor: - hsl(300, 3%, 94%);
    ----orangeColor: hsl(38.8, 100%, 50%);
    /* background: linear-gradient(48deg, rgb(114, 145, 255), rgb(42, 73, 198)); */

    /* ALL project font-sizes  */

    --biggestFontSize: 2rem;
    --h1FontSize: 1.6rem;
    --h2FontSize: 1.25rem;
    --h3FontSize: 1rem;
    --normalFontSize: 0.938rem;
    --smallFontSize: 0.813rem;
    --smallestFontSize: 0.75rem;
    --borderRadius: 10px;
}

/* START: Footer Style =========================>  */

.Footer{
    background: var(---primaryColorLight);
    padding: 2rem 0;

    .secContainer{
        gap: 1.5rem;
        text-align: center;
        align-items: center;

        .logoDiv{
            padding: 1rem;
            .footerLogo{
                display: flex;
                color: var(----primaryColor);
                font-weight: 500;
                cursor: pointer;
                align-items: center;
                justify-content: center;
                padding-bottom: 0.5rem;
                font-size: var(--h1FontSize);

                .icon{
                    font-size: 30px;
                    color: var(----primaryColor);
                }
            }
            p{
                max-width: 200px;
                text-align: center;
                margin: auto;
                padding: 1rem 0;
                line-height: 1rem 0;
                color: var(--primaryColorDark);
                opacity: .5;
                font-weight: 500;
            }
            .socials{
                gap: .5rem;
                margin: auto;
                align-items: center;
                justify-content: center;

                .icon{
                    font-size: 30px;
                    background: var(----primaryColor);
                    border-radius: var(--borderRadius);
                    padding: 7px;
                    color: var(---whiteColor);
                    &:hover{
                        background: var(--primaryColorDark);
                    }
                }
            }
            
        }
        .footerLinks{
            display: grid;
            row-gap: 1rem;

            .linkTitle{
                padding-bottom: .5rem;
                color: var(--primaryColorDark);
                font-weight: 700;
            }
            li{
                &:hover{
                    transform: translateX(10px);

                    a{
                        color: var(----primaryColor);
                        opacity: 1;
                    }
                }
            }

            a, .phone, .email, .site{
                color: var(--primaryColorDark);
                opacity: .6;
                font-size: 13px;
                font-weight: 500;
            }
        }
    }
}

/* MEDIA QUERIES =====================================================> */

@media screen and (min-width:448px){
    .Footer{
        .secContainer{
            grid-template-columns: repeat(2,1fr);
            text-align: start;
            align-items: flex-start;

            .footerLinks{
                justify-content: center;
            }
        }
    }
}


@media screen and (min-width:640px){
    .Footer{
        .secContainer{
            grid-template-columns: repeat(4,1fr);           
        }
    }
}


@media screen and (min-width:960px){
    .Footer{
        .secContainer{
            padding: 2rem 0;
            grid-template-columns: repeat(4,1fr); 
            
            .footerLinks{
                li{
                    padding: .2rem;
                }
            }
        }
    }
}