@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;500;700;900&family=Poppins:wght@300;400;500;600;700&display=swap');
/* font-family: 'Montserrat', sans-serif; */

:root {
    /* ALL project colors */

    ----primaryColor: hsl(228, 66%, 53%);
    ---primaryColorLight: hsl(223, 100%, 97%);
    --primaryColorDark: hsl(216, 58%, 28%);
    ---gradientLight: rgb(114, 145, 255);
    --bgColor: hsl(0, 0%, 100%);
    ---formColor: hsl(20, 42%, 87%);
    ---whiteColor: hsl(240, 25%, 97%);
    ---blackColor: hsl(0, 0%, 18%);
    --blackColorLight: hsl(233, 3%, 51%);
    ---shadowColor: hsl(219, 63%, 95%);
    --inputColor: hsl(330, 12%, 97%);
    ---cardColor: - hsl(300, 3%, 94%);
    ----orangeColor: ☐hsl(25, 84%, 53%);
    /* background: linear-gradient(48deg, rgb(114, 145, 255), rgb(42, 73, 198)); */

    /* ALL project font-sizes  */

    --biggestFontSize: 2.5rem;
    --h1FontSize: 1.6rem;
    --h2FontSize: 1.25rem;
    --h3FontSize: 1rem;
    --normalFontSize: 0.938rem;
    --smallFontSize: 0.813rem;
    --smallestFontSize: 0.75rem;
    --borderRadius: 10px;
}

/* START =============================> */

.Navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 1.5rem 2rem;
    position: fixed;
    background: transparent;
    z-index: 1000;

    .logoDiv {
        display: flex;
        align-items: center;
        justify-content: center;

        .icon {
            color: var(---whiteColor);
            margin-right: .5rem;
        }

        .logo {
            color: var(---whiteColor);
            font-size: var(--h2FontSize);
            justify-content: center;
            font-weight: 500; 
        } 
    }

    @media screen and (max-width:768px) {
        .menu {
            position: absolute;
            width: 70%;
            top: -500px;
            right: 50%;
            left: 50%;
            transform: translate(-50%);
            background: linear-gradient(48deg, rgb(114, 145, 255), rgb(42, 73, 198));
            padding: 2rem;
            border-radius: var(--borderRadius);
            transition: .5s ease-in-out;
            box-shadow: 0 2px 25px -5px var(---shadowColor);

            .nav-text{
                font-size: var(--h3FontSize);
                font-weight: 500;
                padding: 1rem 0;
                text-align: center;
                color: var(---whiteColor);
                cursor: pointer;

                &:hover{
                    color: var(---primaryColorLight);
                    border-bottom: solid 1px var(---primaryColorLight);
                }
            }
        }
    }

    .menuIcon, .closeIcon{
        color: var(---whiteColor);
        font-size: var(--h1FontSize);
        &:hover{
            color: var(--primaryColorDark);
        }
    }
    .closeIcon{
        position: absolute;
        bottom: -25px;
        right: 50%;
        left: 50%;
        transform: translate(-50%);
        background: linear-gradient(48deg, rgb(114, 145, 255), rgb(42, 73, 198));
        padding: 10px;
        height: 50px;
        width: 50px;
        border-radius: 50%;
        box-shadow: 0 2px 4px var(---shadowColor);
    }
    .showNavbar{
        top:8rem;
    }
}
.addBackground{
    background: var(---whiteColor);
    transition: .2s ease-in-out;

    .logoDiv {
        .icon {
            color: var(--primaryColorDark);
        }

        .logo {
            color: var(--primaryColorDark);
        }
    }
    .menuIcon{
        color: var(--primaryColorDark);
    }
    .menu{
        ul{
            .nav-text{
                color: var(--primaryColorDark) !important;
            }
        }
    }
}

/* MEDIA QUERIES =======================================================> */

@media screen and (min-width:768px){
    .Navbar{
        .logoDiv{
            .logo{
                font-size: var(--h2FontSize);
            }
        }
        .closeIcon, .menuIcon{
            display: none;
        }

        .menu{
            ul{
                display: flex;
                gap: 3rem;

                .nav-text{
                    font-size: var(--h3FontSize);
                    font-weight: 500;
                    padding: 1rem 0;
                    text-align: center;
                    cursor: pointer;
                    color: var(---whiteColor);
                    opacity: .5;

                    &:hover{
                        color: var(----primaryColor);
                        opacity: 1;
                        border-bottom: solid 1.5px var(----primaryColor);
                    }
                }
            }
        }
    }
}


@media screen and (min-width:960px){
    .Navbar{
        padding: 2rem 10rem;
    }
}

@media screen and (min-width:1240px){
    .Navbar{
        padding: 2rem 14rem;
    }
}

@media screen and (min-width:1440px){
    .Navbar{
        .logoDiv {   
            .icon {
              font-size: var(--h1FontSize);  
            }
    
            .logo {
              font-size: var(--h1FontSize);  
            } 
        }  
    }
}