/* Font style */

@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;500;700;900&family=Poppins:wght@300;400;500;600;700&display=swap');
/* font-family: 'Montserrat', sans-serif; */

:root {
  /* ALL project colors */

  ----primaryColor: hsl(228, 66%, 53%);
  ---primaryColorLight: hsl(223, 100%, 97%);
  --primaryColorDark: hsl(216, 58%, 28%);
  ---gradientLight: rgb(114, 145, 255);
  --bgColor: hsl(0, 0%, 100%);
  ---formColor: hsl(20, 42%, 87%);
  ---whiteColor: hsl(240, 25%, 97%);
  ---blackColor: hsl(0, 0%, 18%);
  --blackColorLight: hsl(233, 3%, 51%);
  ---shadowColor: hsl(219, 63%, 95%);
  --inputColor: hsl(330, 12%, 97%);
  ---cardColor: - hsl(300, 3%, 94%);
  ----orangeColor: 	hsl(38.8, 100%, 50%);
  /* background: linear-gradient(48deg, rgb(114, 145, 255), rgb(42, 73, 198)); */

  /* ALL project font-sizes  */

  --biggestFontSize: 2.5rem;
  --h1FontSize: 1.6rem;
  --h2FontSize: 1.25rem;
  --h3FontSize: 1rem;
  --normalFontSize: 0.938rem;
  --smallFontSize: 0.813rem;
  --smallestFontSize: 0.75rem;
  --borderRadius: 10px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  transition: 3s ease;
  font-family: 'Montserrat', sans-serif;
}

.flex {
  display: flex;
  align-items: center;
}

.grid {
  display: grid;
  align-items: center;
}

body {
  overflow-x: hidden;
}

p {
  font-size: var(--normalFontSize);
  color: var(--primaryColorDark);
  opacity: .7;
  font-weight: 400;
  line-height: 25px;
}

html {
  font-size: 90%;
}

a {
  text-decoration: none;
}

li {
  list-style: none ;
}

.icon {
  font-size: var(--h2FontSize);
  cursor: pointer;
}

.icons {
  gap: .5rem;

  .icon {
    height: 35px;
    width: 35px;
    background: var(---primaryColorLight);
    padding: 2px;
    border: 2px solid transparent;

    &:hover {
      border: 2px solid var(---shadowColor);
    }
  }
}

.title {
  padding: .5rem 0 1rem;
  color: var(--primaryColorDark);
}

.orangeText{
  color: var(----orangeColor);
  text-transform: capitalize;
  font-weight: 500;
}

.orangeDot{
  color: var(----orangeColor);
  font-size: var(--h1FontSize);
}

img{
  height: auto;
  width: 100%;
}

.btn{
  border: none;
  outline: none;
  cursor: pointer;
  border-radius: 5px;
  padding: .8rem 1.2rem;
  color: var(---whiteColor);
  font-weight: 500;
  background: linear-gradient(48deg, rgb(114, 145, 255), rgb(42, 73, 198));
  &:hover{
    transition: .3s ease-in-out;
    background: var(----primaryColor);
    color: var(---whiteColor);
  }
}

.secTitle{
  display: grid;
  text-align: center;
  padding: 1rem 0;
  font-size: var(--h3FontSize);
  font-weight: 700;
  color: var(--primaryColorDark);
  flex-direction: column;
  width: 100%;

  h3{
    font-size: var(--h1FontSize);
  }
}

.secHeader{
  padding-bottom: 1.5rem;
}

.section{
  padding: 4rem 0 2rem;
}

.container{
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

::selection{
  background: var(--primaryColorDark);
  color: var(---whiteColor);
}

::-webkit-scrollbar{
  width: 10px;
  background: var(---primaryColorLight);
}

::-webkit-scrollbar-thumb{
  width: 10px;
  background: var(----primaryColor);
  border-radius: var(--borderRadius);
}

/* Media Queries ======================================================> */

@media screen and (min-width:840px) {
  .section{
    padding: 4rem 0 3rem;
  }
  
  .container{
    width: 75%;
    margin: auto;
  }
}